<template>
    <view class="uploader-bd" :style="'justify-content:' + option.textAlign + ';'">
        <view 
        v-for="(item, index) in upload_before_list" 
        :key="index" style="display: flex; width: 100%;">
            <view 
            class="uploader-files" 
            @click="previewImage(index)">
                <image 
                    class="uploader-img" 
                    :src="item[website.callbackLinkKey] || item.path"
                    mode="widthFix" />
            </view>
        </view>
    </view>
</template>

<script>
    import website from '@/uni_modules/wangjichao-w_editor/config.js'
    import { Base64 } from '@/uni_modules/wangjichao-w_editor/static/cdn/base64.js';
    const authorization = `Basic ${Base64.encode(`${website.clientId}:${website.clientSecret}`)}`

    export default {
        props: {
            value: {
              type: [String, Number, Object, Array, Boolean],
              default: undefined,
            },
            option: {
                type: Object,
                default: {},
            },
            // 服务器url
            // url: {
            //     type: String,
            //     default: website.baseUrl + website.uploadPath
            // },
            // 上传样式宽高
            upload_img_wh: {
                type: String,
                default: 'width:400rpx;height:400rpx;'
            },
            // 上传数量
            upload_count: {
                type: [Number, String],
                default: 30
            },
            // 是否自动上传? 可以先用变量为false然后再改为true即为手动上传
            upload_auto: {
                type: Boolean,
                default: website.uploadAuto
            },
            // 是否显示删除
            upimg_move: {
                type: Boolean,
                default: true
            },
            // 服务器预览图片
            upimg_preview: {
                type: Array,
                default: () => {
                    return [];
                }
            },
            // 服务器返回预览(看服务器卡顿情况设定)
            upimg_delaytime: {
                type: [Number, String],
                default: 300
            },
            readOnly: {
                type: Boolean,
                default: {},
            },
            init: {
                type: Boolean,
                default: false
            },
        },
        computed: {
            // 请求头信息
            header() {
                return {
                    'Authorization': authorization,
                    'Blade-Auth': `bearer ${this.token}`//this.vuex_token}`
                }
            }
        },
        data() {
            return {
                website: website,
                upload_len: 0,
                upload_cache: [],
                upload_cache_list: [],
                upload_before_list: [],
                url: '',
                token: '',
            }
        },
        watch: {    
            'option.activation': {
                deep: true,
                handler(e, o) {
                    console.log(e, o,'12312312');
                    if(e != o && !this.readOnly){
                        this.chooseImage()
                    }
                },
            }
        },
        async created() {
            this.url = this.vuex_config.config.upload.uploadurl
            this.token = this.vuex_token
            let _self = this;
            setTimeout(() => {
                if(this.value && Array.isArray(this.value)){
                    this.upload_before_list = this.upload_before_list.concat(this.value);
                    this.upload_len = this.upload_before_list.length;
                    this.value.map(item => {
                        this.upload_cache_list.push(item);
                    });
                    this.emit();
                }else if(this.init){
                    this.chooseImage()
                }
            }, this.upimg_delaytime);
        },
        mounted() {
            
        },
        methods: {
            chooseImage() {
                // #ifdef APP-PLUS
            	plus.nativeUI.actionSheet({  
            		cancel: '取消',  
            		buttons: [{  
            				title: '拍照',  //相机国际化文字  
            			},  
            			{  
            				title: '从相册选择',  //相册国际化文字  
            			}  
            		]  
            	},  
            	async (tap) => {  
            		if (tap.index === 1) {  
            			let RearCamera = plus.camera.getCamera( 1 );  
            			RearCamera.captureImage((file)=>{  
            				this.upload_before_list.push({
            					path: file,
            					upload_percent: 0
            				});
            				this.upload_cache = [file];
            				this.upload(this.upload_auto);
            			},(err)=>{  
            				console.log(err)  
            			},{"optimize":false})  
            		} else if (tap.index === 2) {  
            			plus.gallery.pick((res)=>{  
            				for (let i = 0, len = res.files.length; i < len; i++) {
            					this.upload_before_list.push({
            						path: res.files[i],
            						upload_percent: 0
            					});
            				}
            				this.upload_cache = res.files;
            				this.upload(this.upload_auto);
            			},(err)=>{  
            				console.log(err)  
            			},{  
            				maximum:this.upload_count - this.upload_before_list.length,  
            				multiple:true,
                            filter:"image",
                            filename: '/w_editor'
            			});  
            		}
            	})
                //#endif
                // #ifdef MP-WEIXIN
                let _self = this;
                wx.chooseMedia({
                    count: 9,
                    mediaType: ['image'],
                    sourceType: ['album', 'camera'],
                    maxDuration: 30,
                    camera: 'back',
                    success(res) {
                        console.log(res)
                        const files = res.tempFiles.map(o => o.tempFilePath)
                        for (let i = 0, len = files.length; i < len; i++) {
                        	_self.upload_before_list.push({
                        		path: files[i],
                        		upload_percent: 0
                        	});
                        }
                        _self.upload_cache = files;
                        _self.upload(_self.upload_auto);
                    }
                })
                //#endif
            },
            upImage(paths,header) {
                let _self = this;
                const promises = paths.map(function(path) {
                    return promisify(upload)({
                        url: _self.url,
                        header: _self.header,
                        path: path,
                        name: 'file',
                        extra: header,
                        _self: _self
                    });
                });
        
                uni.showLoading({
                    title: `Uploading...`
                });
        
                Promise.all(promises)
                    .then(function(data) {
                        uni.hideLoading();
                        _self.upload_cache_list.push(...data);
                        _self.emit();
                    })
                    .catch(function(res) {
                        uni.hideLoading();
                    });
            },
            async upload(upload_auto) {
            	let _self = this;
                if(upload_auto){
                    await _self.upImage(_self.upload_cache,_self.header)
                }else{
                    _self.upload_cache_list = _self.upload_before_list
                    _self.emit();
                    console.warn(`传输参数:this.$refs.xx.upload(true)才可上传,默认false`);
                }
            },
            previewImage(idx) {
                let _self = this;
                let preview = [];
                for (let i = 0, len = _self.upload_before_list.length; i < len; i++) {
                    if (_self.upload_before_list[i][[website.callbackLinkKey]]) {
                        preview.push(_self.upload_before_list[i][[website.callbackLinkKey]]);
                    } else {
                        preview.push(_self.upload_before_list[i].path);
                    }
                    
                }
                console.log(_self.upload_before_list,preview)
                uni.previewImage({
                    current: idx,
                    urls: preview
                });
            },
            removeImage(idx) {
                let _self = this;
                _self.upload_before_list.splice(idx, 1);
                _self.upload_cache_list.splice(idx, 1);
                _self.upload_len = _self.upload_before_list.length;
                _self.emit();
            },
            emit() {
                let _self = this;
                _self.$emit('change', _self.upload_cache_list);
                _self.$emit('input', _self.upload_cache_list);
            },
        }
    };
const promisify = api => {
    return function(options, ...params) {
        return new Promise(function(resolve, reject) {
            api(
                Object.assign({}, options, {
                    success: resolve,
                    fail: reject
                }),
                ...params
            );
        });
    };
};

const upload = function(options) {
    let url = options.url,
        _self = options._self,
        path = options.path,
        name = options.name,
        header = options.header,
        // data = options.data,
        extra = options.extra,
        success = options.success,
        progress = options.progress,
        fail = options.fail;

    const uploadTask = uni.uploadFile({
        url: url,
        filePath: path,
        name: name,
        header,
        formData: extra,
        success: function(res) {
            let data = null
            try {
                const _data = JSON.parse(res.data);
                data = _data.data
            } catch (e) {
                throw (e, data);
            }
            // 根据自己的返回数据做相应判断,服务器返回200即代表成功请求
            if (res.statusCode == 200) {
                if (success) {
                    success(data);
                }
            } else {
                if (fail) {
                    fail(data);
                }
            }
        },
        fail: function(res) {
            console.log(res);
            if (fail) {
                fail(res);
            }
        }
    });
    uploadTask.onProgressUpdate(async function(res) {
        for (let i = 0, len = _self.upload_before_list.length; i < len; i++) {
            _self.upload_before_list[i]['upload_percent'] = await res.progress;
        }
        _self.upload_before_list = _self.upload_before_list;
        _self.upload_len = _self.upload_before_list.length;
    });
};
</script>

<style lang="scss" scoped>
    .uploader-bd {
        margin: 0;
        display: flex;
        flex-wrap: wrap;
        .uploader-files{
            font-size: 0;
            width: 100%;
            .uploader-img{
                width: 100%;
            }
        }
    }
</style>